import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import {
    CCol,
    CContainer,
    CRow,
    CLink,
} from '@coreui/react'
import "src/scss/profile.scss"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faPen} from '@fortawesome/free-solid-svg-icons';
import Avtar from '../../assets/img/avtar.png';
import {Base_URL,headers} from '../../CommonRoutes/Api'
import {getProfile} from '../../CommonRoutes/Constant'
//import ImageUploader from "react-images-upload";
import axios from 'axios';
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';
import CropFreeIcon from '@material-ui/icons/CropFree';
import IconButton from '@material-ui/core/IconButton';

class Myprofile extends Component {
    fileObj = [];
    fileArray = [];
    constructor(props) {
        super(props);
        this.state = {
            pictures:[],
            name:'',
            profile1:[],
            profile2:[],
            file: Avtar,
            profile: [],
            id:localStorage.getItem("user_id"),
            role:localStorage.getItem("role"),
            image:[],
            blocking:false
        }
        // this.handleChange = this.handleChange.bind(this)
        this.uploadMultipleFiles = this.uploadMultipleFiles.bind(this)
        this.uploadFiles = this.uploadFiles.bind(this)
    }   

    componentDidMount() {
           this.setState({blocking:true})
        fetch(Base_URL + "getuserbyid/" +this.state.id).then((response) => {
            response.json().then((result) => {
                console.log(result);
                this.setState({profile:result,blocking:false})
                setTimeout(
                    () => this.setState({ position: 1,blocking:false }), 
                    3000
                  );
                console.log("profile1",this.state.profile[0]);
                console.log("profile2",this.state.profile[1][0]);
                this.setState({profile1:this.state.profile[0],profile2:this.state.profile[1][0]})
               
            }) 
            
        })      

        getProfile(this.state.id).then((profile) => { this.setState({ name:profile.name }); });
    }

    
    render() {
        const { file } = this.state;
        return (
           
            <div className="c-app c-default-layout flex-row align-items-center">
                <CContainer style={{ padding: "10px" }}>
                <NavLink exact to="/generateQr">
                    <IconButton aria-label="show 4 new mails" color="inherit">
                                <CropFreeIcon />
                                <span> My QR Code</span>
                            </IconButton>
                </NavLink>
                    <CRow className="justify-content-center">
                        <CCol md="12" lg="12" xl="12">
                            <CRow>
                                <CCol md="12" lg="12" xl="12" className="content-center profile_text">
                                    <h4 className="profiletext" >My Profile</h4>
                                </CCol>
                            </CRow>
                            <CRow className="content-center" style={{ backgroundColor: "white", color: "black" }}>
                                <CCol md="12" lg="12" xl="12">

                                    <div className="container">
                                        <div className="picture-container">
                                            <div className="picture">
                                                <img src={file} style={{ width: '106px', height: '106px' }} alt="" />
                                                <input type="file"
                                                    accept="image/*"
                                                    className=""
                                                    onChange={this.handleChange}
                                                />
                                                 
                                              </div>
                                            
                                        </div>
                                        <h5 style={{boder:"2px solid blue",borderRadius:"15px"}}>Change</h5>
                                     </div>
                                             
                                              </CCol>
                                <CCol col="12" sm="12" md="12" xl className="mb-3 mb-xl-0 content-center profile_button">
                                </CCol>
                            </CRow>
                            <CRow style={{ backgroundColor: "white", color: "black" }}>
                                <CCol md="12" lg="12" xl="12" className="justify-content-center  prshadow">
                                    <p className="User_name" style={{ fontWeight: "bold" }}>{this.state.profile1.name}</p>
                                    <p className="User_name" style={{color:"gray",fontWeight:"bold"}}>{this.state.profile1.mobile}</p>
                                    <p className="User_name" style={{ marginBottom: "1rem",color:"gray",fontWeight:"bold" }}>{this.state.profile1.email}</p>
                                    <NavLink to={`/updateprofile/${this.state.id}`}><FontAwesomeIcon icon={faPen} style={{ fontSize: "1.8rem", marginTop: "-3.7rem", position: "relative", float: "right" }} color="gray" /></NavLink>


                                </CCol>
                            </CRow>
                            <CRow >
                                <CCol md="12" lg="12" xl="12" className="justify-content-left address_bar">
                                    <h4 className="myprofiletext" >Stores Details</h4>
                                </CCol>
                            </CRow>
                            <CRow style={{ backgroundColor: "white", color: "black" }}>
                                <CCol md="12" lg="12" xl="12" className="justify-content-center addressUser">
                                <p className="addressText" style={{color:"gray",fontWeight:"bold"}}>{this.state.profile2.CompanyName}</p>
                                </CCol>
                            </CRow>
                            <CRow >
                                <CCol md="12" lg="12" xl="12" className="justify-content-left address_bar">
                                    <h4 className="myprofiletext" >Address</h4>
                                </CCol>
                            </CRow>
                            <CRow style={{ backgroundColor: "white", color: "black" }}>
                                <CCol md="12" lg="12" xl="12" className="justify-content-center addressUser">
                                <p className="addressText" style={{color:"gray",fontWeight:"bold"}}>{this.state.profile2.Address}&nbsp;
                                    <span>{this.state.profile2.City}</span>&nbsp;
                                    <span>{this.state.profile2.State}</span>
                                    pincode-<span>{this.state.profile2.Pincode}</span>
                                    </p>
                                </CCol>
                            </CRow>
                            <CRow >
                                <CCol md="12" lg="12" xl="12" className="justify-content-left address_bar">
                                    <h4 className="myprofiletext" >About Stores</h4>
                                </CCol>
                            </CRow>
                            <CRow style={{ backgroundColor: "white", color: "black" }}>
                                <CCol md="12" lg="12" xl="12" className="justify-content-center addressUser">
                                <p className="addressText" style={{color:"gray",fontWeight:"bold"}}>{this.state.profile2.AboutVendor}</p>
                                </CCol>
                            </CRow>
                            
                            <CRow >
                                <CCol md="12" lg="12" xl="12" className="justify-content-left address_bar">
                                    <h4 className="myprofiletext" >Stores Gallery</h4>
                                </CCol>
                            </CRow>
                            <CRow style={{ backgroundColor: "white", color: "black" }}>
                                <CCol md="12" lg="12" xl="12" className="justify-content-center addressUser">
            <form>
                <div className="form-group multi-preview">
                <div style={{position:'relative',display:'flex'}}>
                    {(this.fileArray || []).map((url,i) => (
                        <div>
                        <img src={url} alt="..." width="100px" height="100px" />
                        <button style={{position:'absolute',right:0}} onClick={(i)=>this.fileArray.splice(i,1)}>&times;</button>
                        </div>
                    ))}
                    </div>
                </div>

                <div className="form-group">
                    <input type="file" className="form-control" onChange={this.uploadMultipleFiles} multiple />
                </div>
                <button type="button" className="btn btn-danger btn-block" onClick={this.uploadFiles}>Upload</button>
            </form>
                                </CCol>
                            </CRow>
                           
                        </CCol>
                    </CRow>
                </CContainer>
                <BlockUI blocking={this.state.blocking} />
            </div>
        );
    }

    uploadFiles(e) {
        let form_data = new FormData();
        this.state.images.forEach((data,i) => {
            form_data.append('image'+(i+1), data, data.name);  
        });
        form_data.append("vendor_id",localStorage.getItem('user_id'))
        
        let url = Base_URL+"addvendor_banner_images";
        axios.post(url, form_data,headers)
            .then(res => {
                console.log(res.data);
                if (res.data && res.data.message === "NEW vendor_product created") {
                  this.componentDidMount();
                    this.handleScreen();
                }
            })
        .catch(err => console.log(err))
    }

    uploadMultipleFiles(e) {
        this.fileObj.push(e.target.files)
        for (let i = 0; i < this.fileObj[0].length; i++) {
            this.fileArray.push(URL.createObjectURL(this.fileObj[0][i]))
        }
        this.setState({
            images: e.target.files
        })
    } 
    
}

export default Myprofile;